<template>
    <div class="body-bg">
        <div class="form-item">
            <h2 class="form-header">基本信息</h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.type"
                        label="领料类型："
                        disabled
                        label-align="left"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.subject"
                        label="对方科目："
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showSubjectPicker=true"
                    />
                    <van-popup v-model="showSubjectPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="subjects"
                            @cancel="showSubjectPicker = false"
                            @confirm="onBSubjectConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.dept"
                        label="领料部门："
                        disabled
                        label-align="left"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.purpose"
                        label="领料用途："
                        label-align="left"
                        autosize
                        rows="1"
                        type="textarea"
                    />
                </van-col>
            </van-row>
        </div>
        
        <div class="form-item">
            <h2 class="form-header">物料明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialNumber"
                                label="物料编码："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialBatch"
                                label="批号："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockName"
                                label="仓库："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockPlace"
                                label="仓位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                required
                                v-model="item.materialSum"
                                label="申请数量："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.materialActualSum"
                                label="实发数量："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial">删除</van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">提交</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            baseData:{
                type:'',
                typeId:'',
                subject:'',
                subjectId:'',
                purpose:'',
                dept:'',
                deptId:'',
            },
            activeName:'',
            showSubjectPicker:false,
            materialList:[],
            subjects:[{id:1,text:133},{id:2,text:13}]
        };
    },
    created(){
        this.searchProFeedInfo();
        this.searchAllAccount();
    },
    methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        searchProFeedInfo(){
            this.showLoading('正在获取数据...');
            this.$api.get("/searchProFeedInfo",{feedNumber:this.$route.query.number},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.materialList=response.data.data;
                        this.baseData.type=response.data.typeName;
                        this.baseData.typeId=response.data.type;
                        this.baseData.dept=response.data.dept;
                        this.baseData.deptId=response.data.deptId;
                        Toast.clear();
                    } else{
                        Toast.fail(response.data.msg);
                        Toast.clear();
                        return;
                    }
                } else {
                    Toast.fail("数据获取异常！");
                    Toast.clear();
                    return;
                }
            });
        },
        searchAllAccount(){
            this.showLoading('正在获取数据...');
            this.$api.get("/searchAllAccount",{},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.subjects=response.data.subjects;
                        Toast.clear();
                    } else{
                        Toast.fail(response.data.msg);
                        Toast.clear();
                        return;
                    }
                } else {
                    Toast.fail("数据获取异常！");
                    Toast.clear();
                    return;
                }
            });
        },
        onSubmit() {
            Toast.loading({
                duration:0,
                message: '正在提交数据...',
                forbidClick: true
            });
            console.log(this.materialList);
            console.log(this.baseData);
            this.$api.post("/submitProductRequisit",{baseData:JSON.stringify(this.baseData),materialList:JSON.stringify(this.materialList)},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("数据提交成功");
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("数据提交异常！");
                    return;
                }
            });
        },
        onBack(){
            this.$router.go(-1);
        },
        onBSubjectConfirm(data){
            this.baseData.subject=data.text;
            this.baseData.subjectId=data.id;
            this.showSubjectPicker=false;
        },
        deleteMaterial(){
            console.log(this.activeName);
            this.materialList.some((item, i)=>{
                console.log(item.id);
　　　　　　    if(item.id==this.activeName){
　　　　　　　　    this.materialList.splice(i, 1)
　　　　　　　　　　return true
　　　　　　　　　}
　　　　　　　})
        }
    }

}
</script>



