<template>
    <div>
        <van-search
            v-model="feedNumber"
            placeholder="请输入生产投料单编码"
            show-action
            shape="round"
            clear
            @search="onSearch"
            style="margin-right:1rem;margin-left:0.1rem"
            right-icon
            >
            <div slot="action" disabled="loading" @click="onSearch">搜索</div>
        </van-search>
        <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
            style="margin-right:1rem;margin-left:0.1rem"
            >
            <van-cell v-for="item in list" :key="item" :title="item" @click="createProRequisit(item)"/>
        </van-list>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            feedNumber:'',
            list: [],
            loading: false,
            finished: false
        };
  },
  methods: {
        onLoad() {
            this.onSearch();
        },
        onSearch(){
            Toast.loading({
                duration:0,
                message: '加载中...',
                forbidClick: true
            });
            this.$api.get("/searchAllProFeed",{feedNumber:this.feedNumber},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.list=response.data.msg;
                    }else{
                        Toast.fail(response.data.msg);
                    }
                } else {
                    Toast.fail('获取数据时出现异常，请重新操作');
                }
            });
            this.finished=true;
        },
        createProRequisit(data){
            this.$router.push({path:'/producionFeedingForProductRequisit',query:{'number':data}});
        }
    }
}
</script>


